<template>
  <div id="app" :v-if="dl" :style="theme.app">
    <span id="status">
      <Sign/>{{ jdat.status }}
    </span>
    <header id="page">
      <img/>
      <p></p>
      <vs-button-group>
        <vs-button relief @click="chartActive = true">
          <i class='bx bx-home-alt' ></i> Home
        </vs-button>
        <vs-button relief>
          <i class='bx bxs-phone-call' ></i> Contact
        </vs-button>
        <vs-button relief loading>
          <i class='bx bxs-cart-alt' ></i> Products
        </vs-button>
        <vs-button relief upload>
          <i class='bx bxs-envelope' ></i> Send
        </vs-button>
      </vs-button-group>
      <vs-button
          color="rgb(59,222,200)"
          gradient
          @click="loginActive = true"
      >
          Login
      </vs-button>
      <vs-switch dark v-model="th" style="width:60px;margin: 0.5rem">
        <template >
          <Sun v-if="!th" ></Sun>
          <Moon v-else style="background-color:transparent;color:white"></Moon>
        </template>
      </vs-switch>
    </header>

    <vs-dialog not-close auto-width not-padding v-model="chartActive">
      <div class="con-image" :v-html="jdat.plot">
      </div>
    </vs-dialog>

    <vs-dialog prevent-close v-model="loginActive">
      <template #header>
        <h4 class="not-margin">
          Welcome to <b>Vuesax</b>
        </h4>
      </template>

      <div class="con-form">
        <vs-input v-model="input1" placeholder="Email">
          <template #icon>
            @
          </template>
        </vs-input>
        <vs-input type="password" v-model="input2" placeholder="Password">
          <template #icon>
            <i class='bx bxs-lock'></i>
          </template>
        </vs-input>
      </div>

      <template #footer>
        <div class="footer-dialog">
          <vs-button block>
            Sign In
          </vs-button>

          <div class="new">
            New Here? <a href="#" @click="logupActive = true">Create New Account</a>
          </div>
        </div>
      </template>
    </vs-dialog>

    <vs-dialog prevent-close v-model="logupActive">
      <template #header>
        <h4 class="not-margin">
          Welcome to <b>Vuesax</b>
        </h4>
      </template>


      <div class="con-form">
        <vs-input v-model="input1" placeholder="Email">
          <template #icon>
            @
          </template>
        </vs-input>
        <vs-input type="password" v-model="input2" placeholder="Password">
          <template #icon>
            <i class='bx bxs-lock'></i>
          </template>
        </vs-input>
        <div class="flex">
          <vs-checkbox v-model="checkbox1">Remember me</vs-checkbox>
          <a href="#">Forgot Password?</a>
        </div>
      </div>

      <template #footer>
        <div class="footer-dialog">
          <vs-button block>
            Sign In
          </vs-button>

          <div class="new">
            New Here? <a href="#">Create New Account</a>
          </div>
        </div>
      </template>
    </vs-dialog>

    <vs-card-group>
      <Card v-for="card in jdat.news" :key="card" :c="card" v-show="checkImage(card.image_url)"></Card>
    </vs-card-group>
    <Table></Table>
    <Subscribe :theme="theme.sub"></Subscribe>
    <Footer :theme="theme.footer"></Footer>
  </div>
</template>

<script>
import Vue from 'vue'
import Vuesax from 'vuesax'

import 'vuesax/dist/vuesax.css'
//import Navbar from './components/Navbar'
import Sign from 'vue-material-design-icons/CurrencyUsdOff.vue';
import Moon from 'vue-material-design-icons/WeatherNight.vue';
import Sun from 'vue-material-design-icons/WhiteBalanceSunny.vue'
import Card from "./components/Card";
import Table from "./components/Table";
import Subscribe from "./components/Subscribe";
import Footer from "./components/Footer";

Vue.use(Vuesax, {
  colors: {
    primary:'#5b3cc4',
    success:'rgb(23, 201, 100)',
    danger:'rgb(242, 19, 93)',
    warning:'rgb(255, 130, 0)',
    dark:'rgb(36, 33, 69)'
  }
})

export default {
  name: 'App',
  components: {
    Card,
    Table,
    Subscribe,
    Footer,
    Moon,
    Sun,
    Sign,
  },
  created: function(){
    this.fetchURL("news", "GET", 'news')
    this.fetchURL("current_cap/bitcoin", "GET", 'status')
    this.fetchURL("caps_chart/bitcoin", "GET", 'plot')
    this.dl = true
  },
  methods: {
    checkImage: function (url) {
      const img = new Image();
      img.src = url;

      if (img.complete) {
        return true
      } else {
        img.onload = () => {
          return true
        };
        img.onerror = () => {
          return false
        };
      }
    },
    // video:
    fetchURL: async function (url, method='GET', val) {
      let response = await fetch(window.location.href.replace('home', url) , {
        method: method,
            headers: {
          "Access-Control-Allow-Origin": "*",
              "Content-Type": "text/plain"
        }})
      if (response.ok) {
        this.jdat[val] = await response.text()
        return true
      } else {
        return response.status
      }
    },
  },
  data: () => ({
    theme: {
      footer: "",
      app: "",
      sub: null,
    },
    th: false,
    jdat: {
      status: null,
      news: null,
      table: null,
      plot: null,

    },
    loginActive: false,
    logupActive: false,
    chartActive: false,
    dl: false,
  }),
  watch: {
    th(val) {
      if (val) {
        this.theme.footer = `background-color: #282444; color: #ddd`
        this.theme.app = `background-color: #141222; color: #aaa`
        this.theme.sub = `background-color: #141222;`
      } else {
        this.theme.footer = ``
        this.theme.app = ``
        this.theme.sub = ``
      }
    },
  },
}
</script>

